import React, { useMemo } from 'react';
import { X, Check } from 'lucide-react';
import { INITIAL_MATING_PLAN_STATE } from '../../data/mockData';

const MatingPlanResult = ({ planState, setPlanState, setCurrentStep }) => {
    
    const matingResults = useMemo(() => {
        if (planState.selectedCows.length === 0 || planState.selectedSires.length === 0) return [];
        
        return planState.selectedCows.map(cow => ({
            cow,
            recommendedSire: planState.selectedSires[0], //  :     
            matchScore: (Math.random() * 100).toFixed(1), 
            isFlawMatch: planState.selectedSires[0].flawTraits.length > 0 ? ' ' : '',
        }));
    }, [planState.selectedCows, planState.selectedSires]);

    const handleReset = () => {
        setPlanState(INITIAL_MATING_PLAN_STATE);
        setCurrentStep(1);
    };

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
            <h3 style={{ fontSize: '1.25rem', fontWeight: 'bold', color: '#374151' }}>  </h3>

            {matingResults.length === 0 ? (
                <div style={{ textAlign: 'center', padding: '2.5rem', backgroundColor: '#fee2e2', border: '1px solid #fca5a5', borderRadius: '0.5rem', color: '#b91c1c' }}>
                    <X size={40} style={{ margin: '0 auto 1rem auto' }}/>
                    <p style={{ fontWeight: '600', fontSize: '1.125rem' }}>  .</p>
                    <p>    1   .</p>
                </div>
            ) : (
                <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
                    <div style={{ backgroundColor: '#eef2ff', padding: '1rem', borderRadius: '0.5rem', display: 'flex', justifyContent: 'space-around', textAlign: 'center', border: '1px solid #c7d2fe' }}>
                        <div>
                            <p style={{ fontSize: '0.875rem', fontWeight: '500', color: '#4f46e5' }}>  </p>
                            <p style={{ fontSize: '1.5rem', fontWeight: 'bold', color: '#3730a3' }}>{planState.selectedCows.length} </p>
                        </div>
                        <div>
                            <p style={{ fontSize: '0.875rem', fontWeight: '500', color: '#4f46e5' }}>  </p>
                            <p style={{ fontSize: '1.5rem', fontWeight: 'bold', color: '#3730a3' }}>{planState.selectedSires.length} </p>
                        </div>
                        <div>
                            <p style={{ fontSize: '0.875rem', fontWeight: '500', color: '#4f46e5' }}>  </p>
                            <p style={{ fontSize: '1.5rem', fontWeight: 'bold', color: '#3730a3' }}>{planState.selectedCows.length * planState.selectedSires.length} </p>
                        </div>
                    </div>

                    <div style={{ overflowX: 'auto', backgroundColor: '#f9fafb', borderRadius: '0.5rem', border: '1px solid #e5e7eb' }}>
                        <table className="data-table">
                            <thead>
                                <tr>
                                    {[' ID', ' ', '  ID', '  ', '  ', ' '].map((header) => (
                                        <th key={header} className="table-header">{header}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {matingResults.map((result, index) => (
                                    <tr key={index} className="table-row">
                                        <td className="table-cell" style={{ fontWeight: '500', color: '#1f2937' }}>{result.cow.id}</td>
                                        <td className="table-cell" style={{ color: '#4b5563' }}>{result.cow.name}</td>
                                        <td className="table-cell" style={{ fontWeight: '500', color: '#4f46e5' }}>{result.recommendedSire.id}</td>
                                        <td className="table-cell" style={{ color: '#4f46e5' }}>{result.recommendedSire.name}</td>
                                        <td className="table-cell" style={{ fontWeight: 'bold', color: '#10b981' }}>{result.matchScore} / 100</td>
                                        <td className="table-cell">
                                            <span className={`status-badge ${result.isFlawMatch === ' ' ? 'status-attention' : 'status-good'}`}>
                                                {result.isFlawMatch}
                                            </span>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>

                    <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '0.75rem', paddingTop: '1rem' }}>
                         <button
                            onClick={handleReset}
                            style={{ display: 'flex', alignItems: 'center', padding: '0.5rem 1rem', border: '1px solid #d1d5db', color: '#374151', borderRadius: '0.5rem', transition: 'background-color 0.15s', ':hover': { backgroundColor: '#f3f4f6' } }}
                        >
                              
                        </button>
                        <button
                            onClick={() => alert(' .')}
                            style={{ display: 'flex', alignItems: 'center', padding: '0.5rem 1rem', backgroundColor: '#10b981', color: 'white', fontWeight: '600', borderRadius: '0.5rem', transition: 'background-color 0.15s', ':hover': { backgroundColor: '#059669' } }}
                        >
                            <Check size={20} style={{ marginRight: '0.25rem' }}/>  
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default MatingPlanResult;
