import React, { useState, useMemo, useEffect, useRef } from 'react';
import { Search, Filter, Check } from 'lucide-react';
import { mockSires } from '../data/mockData';
import FilterSidebar from '../components/FilterSidebar';
import $ from 'jquery';
import dt from 'datatables.net-dt';
 
// DataTables      
import 'datatables.net-dt/css/dataTables.dataTables.css';
import 'datatables.net-responsive-dt/css/responsive.dataTables.css';

// DataTables    
import 'datatables.net-responsive';

const SireSelectionPage = ({ selectedFarm, selectedSires, onSireAdd }) => {
    const tableRef = useRef(null);
    const dtApi = useRef(null);
    const currentSelectedIdsRef = useRef(new Set()); // currentSelectedIds    ref

    const [isFilterOpen, setFilterOpen] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    
    const farmFilteredSires = useMemo(() => {
        return mockSires.filter(sire => sire.farmId === selectedFarm.id || sire.status === ''); //  
    }, [selectedFarm.id]);

    const [filteredSires, setFilteredSires] = useState(farmFilteredSires); 

    const currentSelectedIds = useMemo(() => new Set(selectedSires.map(s => s.id)), [selectedSires]);

    // currentSelectedIds   ref .
    useEffect(() => {
        currentSelectedIdsRef.current = currentSelectedIds;
        if (dtApi.current) {
            dtApi.current.draw(false); // currentSelectedIds      drawCallback .
        }
    }, [currentSelectedIds]);

    const handleSearch = () => {
        const query = searchQuery.trim().toLowerCase();
        if (!query) {
            setFilteredSires(farmFilteredSires);
            return;
        }
        
        const filtered = farmFilteredSires.filter(sire => 
            sire.id.toLowerCase().includes(query) || 
            sire.name.toLowerCase().includes(query)
        );
        setFilteredSires(filtered);
    };

    const handleFilterApply = (filters) => {
        console.log(" :", filters);
        //     .
        setFilterOpen(false);
    };

    const columns = [
        { title: ' ', data: 'id' },
        { title: '', data: 'name' },
        { title: '', data: 'flawTraits' },
        { title: '', data: 'betacasein' },
        { title: '', data: 'kappacasein' },
        { title: '', data: 'id' } //    id 
    ];

    useEffect(() => {
        const options = {
            columns: columns,
        responsive: {
            details: {
                // 'inline'     +  ,
                //     child row .
                type: 'inline'
            }
        },
        paging: true,
        lengthChange: false,
        pageLength: 10,
        searching: false, //   UI 
        ordering: true,
        language: {
            emptyTable: "  .",
            paginate: {
                first: "",
                last: "",
                next: "",
                previous: ""
            },
            info: "_TOTAL_  _START_ _END_ ",
            infoEmpty: "0",
            infoFiltered: "(_MAX_ )"
        },
        columnDefs: [
            { responsivePriority: 2, targets: 0 }, // ' '     
            {
                targets: 2, // 
                render: (data) => {
                    // data   join ,   '' .
                    return Array.isArray(data) && data.length > 0 ? data.join(', ') : '';
                }
            },
            {
                targets: 5, //  
                // responsivePriority 1      .
                // ( (+) priority 0 )
                responsivePriority: 1,
                orderable: false,
                className: 'dt-body-right',
                render: (data, type, row) => {
                    //   ,   drawCallback .
                    return `<button class="datatable-action-button" data-sire-id="${row.id}"> </button>`;
                }
            }
        ],
        drawCallback: function(settings) {
            const api = this.api();

            //  
            api.responsive.recalc();

            //   : drawCallback   currentSelectedIds  .
            api.rows({ page: 'current' }).nodes().to$().find('.datatable-action-button').each(function() {
                const button = $(this); // jQuery  
                const sireId = button.data('sire-id');
                if (currentSelectedIdsRef.current.has(sireId)) { // ref    
                    button.replaceWith(`<span class="datatable-completed-span"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><polyline points="20 6 9 17 4 12"></polyline></svg>  </span>`);
                }
            });
        }
        };

        // DataTables 
        dtApi.current = $(tableRef.current).DataTable(options);

        //    (    )
        $(tableRef.current).on('click', '.datatable-action-button', function() {
            const sireId = $(this).data('sire-id');
            const sireToAdd = filteredSires.find(s => s.id === sireId);
            if (sireToAdd) {
                onSireAdd(sireToAdd);
                // onSireAdd   currentSelectedIds ,
                //    useEffect dtApi.current.draw(false)  UI .
            }
        });

        //    DataTables  
        return () => {
            $(tableRef.current).off('click');
            dtApi.current.destroy();
        };
    }, []); //       

    //     
    useEffect(() => {
        if (dtApi.current) {
            dtApi.current.clear().rows.add(filteredSires).draw();
        }
    }, [filteredSires]);

    return (
        <div className="page-content" style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
            <h2 className="page-title">  (: {selectedFarm.name})</h2>
            
            <div className="card search-bar-container">
                <div style={{ display: 'flex', flexGrow: 1, gap: '0.5rem' }}>
                    <input
                        type="search"
                        placeholder=" ,  "
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="form-input"
                    />
                    <button
                        onClick={handleSearch}
                        className="action-button"
                        style={{ padding: '0.75rem 1rem' }}
                    >
                        <Search size={20} />
                    </button>
                </div>
                
                <button
                    onClick={() => setFilterOpen(true)}
                    className="secondary-button"
                >
                    <Filter size={20} style={{ marginRight: '0.5rem' }} />
                     
                </button>
            </div>

            <div className="card">
                <h3 style={{ fontSize: '1.125rem', fontWeight: '600', color: '#374151', marginBottom: '0.75rem' }}>  ({filteredSires.length})</h3>
                <div className="table-container">
                    <table ref={tableRef} className="display nowrap" style={{ width: '100%' }}>
                        {/*  DataTables options  . */}
                    </table>
                </div>
            </div>
            
            <FilterSidebar isOpen={isFilterOpen} onClose={() => setFilterOpen(false)} onApply={handleFilterApply} />
        </div>
    );
};

export default SireSelectionPage;
