import React, { useState } from 'react';
import { Filter, X } from 'lucide-react';

const FilterSidebar = ({ isOpen, onClose, onApply }) => {
    //     ()
    const [filters, setFilters] = useState({
        type: '', flaw: '', betacasein: '', kappacasein: '', status: ''
    });

    const handleChange = (e) => {
        setFilters({ ...filters, [e.target.name]: e.target.value });
    };

    const handleApply = () => {
        onApply(filters);
    };

    const handleClear = () => {
        setFilters({ type: '', flaw: '', betacasein: '', kappacasein: '', status: '' });
        onApply({});
    };

    return (
        <>
            {/*  */}
            {isOpen && (
                <div 
                    style={{ position: 'fixed', inset: 0, backgroundColor: 'rgba(17, 24, 39, 0.5)', zIndex: 40 }}
                    onClick={onClose}
                ></div>
            )}
            
            {/*   */}
            <div 
                style={{ 
                    position: 'fixed', 
                    top: 0, 
                    right: 0, 
                    bottom: 0, 
                    width: '20rem', 
                    backgroundColor: 'white', 
                    padding: '1.5rem', 
                    boxShadow: '-4px 0 10px rgba(0, 0, 0, 0.1)', 
                    zIndex: 50, 
                    transform: isOpen ? 'translateX(0)' : 'translateX(100%)',
                    transition: 'transform 0.3s ease-in-out'
                }}
            >
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderBottom: '1px solid #e5e7eb', paddingBottom: '0.75rem', marginBottom: '1.5rem' }}>
                    <h3 style={{ fontSize: '1.25rem', fontWeight: 'bold', color: '#1f2937', display: 'flex', alignItems: 'center' }}><Filter size={20} style={{ marginRight: '0.5rem' }} />  </h3>
                    <button onClick={onClose} style={{ color: '#6b7280', padding: '0.25rem', borderRadius: '50%', transition: 'background-color 0.15s', ':hover': { color: '#1f2937', backgroundColor: '#f3f4f6' } }}>
                        <X size={24} />
                    </button>
                </div>

                <div style={{ display: 'flex', flexDirection: 'column', gap: '1rem', height: 'calc(100vh - 180px)', overflowY: 'auto', paddingRight: '0.5rem' }}>
                    
                    {[
                        { name: 'type', label: '', options: [{ value: 'HO', label: ' (HO)' }, { value: 'JE', label: ' (JE)' }] },
                        { name: 'flaw', label: '', options: [{ value: 'BL', label: 'BL (Bovine Leukocyte)' }, { value: 'CVM', label: 'CVM (Complex Vertebral Malformation)' }] },
                        { name: 'betacasein', label: '', options: [{ value: 'A1A1', label: 'A1A1' }, { value: 'A1A2', label: 'A1A2' }, { value: 'A2A2', label: 'A2A2' }] },
                        { name: 'kappacasein', label: '', options: [{ value: 'AA', label: 'AA' }, { value: 'AB', label: 'AB' }, { value: 'BB', label: 'BB' }] },
                        { name: 'status', label: ' ', options: [{ value: '', label: ' ' }, { value: '', label: ' ' }] },
                    ].map(field => (
                        <div key={field.name} style={{ display: 'flex', flexDirection: 'column', gap: '0.25rem' }}>
                            <label style={{ fontSize: '0.875rem', fontWeight: '600', color: '#374151' }}>{field.label}</label>
                            <select name={field.name} value={filters[field.name]} onChange={handleChange} style={{ width: '100%', padding: '0.5rem', border: '1px solid #d1d5db', borderRadius: '0.375rem', transition: 'border-color 0.15s' }}>
                                <option value=""></option>
                                {field.options.map(option => (
                                    <option key={option.value} value={option.value}>{option.label}</option>
                                ))}
                            </select>
                        </div>
                    ))}
                    
                </div>
                
                <div style={{ position: 'absolute', bottom: 0, left: 0, right: 0, padding: '1.5rem', borderTop: '1px solid #e5e7eb', backgroundColor: 'white', display: 'flex', gap: '0.75rem' }}>
                    <button
                        onClick={handleClear}
                        style={{ flex: 1, padding: '0.5rem 1rem', border: '1px solid #d1d5db', color: '#374151', borderRadius: '0.5rem', transition: 'background-color 0.15s', ':hover': { backgroundColor: '#f9fafb' } }}
                    >
                        
                    </button>
                    <button
                        onClick={handleApply}
                        style={{ flex: 1, padding: '0.5rem 1rem', backgroundColor: '#4f46e5', color: 'white', fontWeight: '600', borderRadius: '0.5rem', transition: 'background-color 0.15s', ':hover': { backgroundColor: '#4338ca' } }}
                    >
                         
                    </button>
                </div>
            </div>
        </>
    );
};

export default FilterSidebar;
