import React, { useState, useMemo, useEffect, useCallback } from 'react';
import { mockFarms, INITIAL_MATING_PLAN_STATE } from './data/mockData';
import './styles/styles.css';
import CustomDialog from './components/CustomDialog';
import Header from './components/Header';
import Sidebar from './components/Sidebar';
import LoginPage from './pages/LoginPage';
import FarmSearchPage from './pages/FarmSearchPage';
import SireSelectionPage from './pages/SireSelectionPage';
import MatingPlanPage from './pages/MatingPlanPage';
import InventoryPage from './pages/InventoryPage';

// --- Main Application Component ---

const App = () => {
    const [isLoggedIn, setIsLoggedIn] = useState(false); //      false 
    const [currentPage, setCurrentPage] = useState('farmSearch');
    const [isSidebarOpen, setSidebarOpen] = useState(false);
    const [isSidebarCollapsed, setSidebarCollapsed] = useState(false);
    const [selectedFarm, setSelectedFarm] = useState({}); //   
    const [matingPlanState, setMatingPlanState] = useState(INITIAL_MATING_PLAN_STATE);
    const [dialog, setDialog] = useState({ isOpen: false, title: '', message: '', onConfirm: null, onClose: null });
    
    //       .
    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth < 1024) {
                setSidebarCollapsed(true);
            } else {
                setSidebarCollapsed(false);
            }
        };

        window.addEventListener('resize', handleResize);
        handleResize(); //    

        return () => window.removeEventListener('resize', handleResize);
    }, []);

    //   ID
    const currentFarmId = selectedFarm.id;
    
    //     ( )
    const handleSireAdd = useCallback((sire) => {
        if (matingPlanState.selectedSires.some(s => s.id === sire.id)) {
            setDialog({ isOpen: true, title: "", message: `'${sire.id}'    .`, onClose: () => setDialog({ isOpen: false }) });
            return;
        }

        setMatingPlanState(prev => ({ 
            ...prev, 
            selectedSires: [...prev.selectedSires, sire] 
        }));
        setDialog({ isOpen: true, title: " ", message: `'${sire.id}'    .`, onClose: () => setDialog({ isOpen: false }) });
    }, [matingPlanState.selectedSires, setDialog]);


    //        (Sidebar )
    const checkFarmSelectionAndNavigate = (pageId, requiredFarm) => {
        if (requiredFarm && !selectedFarm.id) {
            setDialog({
                isOpen: true,
                title: "  ",
                message: "        .   .",
                onConfirm: () => {
                    setDialog({ isOpen: false });
                    setCurrentPage('farmSearch');
                },
                onClose: () => setDialog({ isOpen: false }),
            });
            return;
        }
        setCurrentPage(pageId);
        setSidebarOpen(false); //     
    };
    
    
    //   
    const renderContent = () => {
        switch (currentPage) {
            case 'farmSearch':
                return <FarmSearchPage 
                    selectedFarm={selectedFarm} 
                    setSelectedFarm={setSelectedFarm} 
                    setCurrentPage={checkFarmSelectionAndNavigate}
                />;
            case 'sireSelection':
                return <SireSelectionPage 
                    selectedFarm={selectedFarm} 
                    selectedSires={matingPlanState.selectedSires}
                    onSireAdd={handleSireAdd}
                />;
            case 'matingPlan':
                return <MatingPlanPage 
                    planState={matingPlanState} 
                    setPlanState={setMatingPlanState} 
                    selectedFarm={selectedFarm}
                    setDialog={setDialog}
                />;
            case 'inventory':
                return <InventoryPage />;
            default:
                return <div>   .</div>;
        }
    };

    if (!isLoggedIn) {
        return <LoginPage onLogin={() => {
            setIsLoggedIn(true);
            setSelectedFarm(mockFarms[0]); //       
            setCurrentPage('farmSearch');
        }} />;
    }

    return (
        <div className={`app-container ${isSidebarCollapsed ? 'sidebar-collapsed' : ''}`}>
            {/* Sidebar */}
            <Sidebar 
                isSidebarOpen={isSidebarOpen} 
                setSidebarOpen={setSidebarOpen} 
                currentPage={currentPage} 
                setCurrentPage={checkFarmSelectionAndNavigate}
                setIsLoggedIn={setIsLoggedIn}
                selectedFarm={selectedFarm}
                isCollapsed={isSidebarCollapsed}
                setCollapsed={setSidebarCollapsed}
            />

            {/* Main Content Area */}
            <div className="main-content-area">
                {/* Header */}
                <Header 
                    toggleSidebar={() => {setSidebarOpen(prev => !prev); setSidebarCollapsed(false);}} 
                    setIsLoggedIn={setIsLoggedIn}
                    selectedFarm={selectedFarm}
                    currentFarmId={currentFarmId}
                />

                {/* Page Content */}
                <main className="page-content">
                    {renderContent()}
                </main>
            </div>
            
            {/* Custom Dialog / Modal */}
            <CustomDialog 
                title={dialog.title} 
                message={dialog.message} 
                isOpen={dialog.isOpen} 
                onClose={dialog.onClose} 
                onConfirm={dialog.onConfirm} 
            />
        </div>
    );
};

export default App;
