import React from 'react';
import { Menu, LogOut, Check, X } from 'lucide-react';

const Header = ({ toggleSidebar, setIsLoggedIn, selectedFarm, currentFarmId }) => (
    <header className="header">
        <div className="header-left">
            <button onClick={toggleSidebar} className="hamburger-button">
                <Menu size={24} />
            </button>
            <h1 className="header-title">   </h1>
        </div>
        
        <div className="farm-info">
            {selectedFarm.id ? (
                <div className="farm-info-badge selected">
                    <Check size={16} style={{ marginRight: '0.25rem' }} />
                    <span> : {selectedFarm.name} ({currentFarmId})</span>
                </div>
            ) : (
                <div className="farm-info-badge not-selected">
                    <X size={16} style={{ marginRight: '0.25rem' }} />
                    <span>  </span>
                </div>
            )}
        </div>
    </header>
);

export default Header;
