import React, { useState, useEffect, useRef } from 'react';
import { Archive, Plus, Trash2 } from 'lucide-react';
import $ from 'jquery';
import 'datatables.net-dt';

// DataTables      
import 'datatables.net-dt/css/dataTables.dataTables.css';
import 'datatables.net-responsive-dt/css/responsive.dataTables.css';

// DataTables    
import 'datatables.net-responsive';

//   
const initialInventory = [
    { id: 1, name: '', type: '', quantity: 10, receivedDate: '2023-10-01' },
    { id: 2, name: '', type: '', quantity: 5, receivedDate: '2023-10-15' },
    { id: 3, name: '-A', type: '', quantity: 3, receivedDate: '2023-11-01' },
];

const InventoryPage = () => {
    const tableRef = useRef(null);
    const dtApi = useRef(null);
    const [inventory, setInventory] = useState(initialInventory);
    const [newItem, setNewItem] = useState({ name: '', type: '', quantity: '', receivedDate: '' });

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setNewItem(prev => ({ ...prev, [name]: value }));
    };

    const handleAddItem = (e) => {
        e.preventDefault();
        if (!newItem.name || !newItem.quantity || !newItem.receivedDate) {
            alert('  .');
            return;
        }
        const newEntry = {
            id: Date.now(), //  ID 
            ...newItem,
            quantity: parseInt(newItem.quantity, 10),
        };
        setInventory(prev => [newEntry, ...prev]);
        setNewItem({ name: '', type: '', quantity: '', receivedDate: '' }); //   
    };

    const handleDeleteItem = (id) => {
        if (window.confirm('   ?')) {
            setInventory(prev => prev.filter(item => item.id !== id));
        }
    };

    const columns = [
        { title: '', data: 'name' },
        { title: '', data: 'type' },
        { title: '', data: 'quantity' },
        { title: '', data: 'receivedDate' },
        { title: '', data: 'id' }
    ];

    useEffect(() => {
        const options = {
            columns: columns,
            responsive: { details: { type: 'inline' } },
            paging: true,
            lengthChange: false,
            pageLength: 5,
            searching: false,
            ordering: true,
            language: {
                emptyTable: "  .",
                paginate: { first: "", last: "", next: "", previous: "" },
                info: "_TOTAL_  _START_ _END_ ",
                infoEmpty: "0",
            },
            columnDefs: [
                { responsivePriority: 2, targets: 0 },
                {
                    targets: 4, // () 
                    responsivePriority: 1,
                    orderable: false,
                    className: 'dt-body-center',
                    render: (data) => `<button class="datatable-action-button danger-button" data-item-id="${data}"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><polyline points="3 6 5 6 21 6"></polyline><path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path></svg></button>`
                }
            ],
            drawCallback: function() {
                this.api().responsive.recalc();
            }
        };

        dtApi.current = $(tableRef.current).DataTable(options);

        $(tableRef.current).on('click', '.datatable-action-button', function() {
            const itemId = $(this).data('item-id');
            handleDeleteItem(itemId);
        });

        return () => {
            if (dtApi.current) {
                $(tableRef.current).off('click');
                dtApi.current.destroy();
                dtApi.current = null;
            }
        };
    }, []);

    useEffect(() => {
        if (dtApi.current) {
            dtApi.current.clear().rows.add(inventory).draw();
        }
    }, [inventory]);

    return (
        <div className="page-content" style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
            <h2 className="page-title"> </h2>

            {/*    */}
            <div className="card">
                <h3 style={{ fontSize: '1.125rem', fontWeight: '600', color: '#374151', marginBottom: '1rem' }}>  </h3>
                <form onSubmit={handleAddItem} style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(150px, 1fr))', gap: '1rem', alignItems: 'flex-end' }}>
                    <div>
                        <label htmlFor="name" className="form-label"></label>
                        <input id="name" name="name" type="text" value={newItem.name} onChange={handleInputChange} className="form-input" placeholder=": " />
                    </div>
                    <div>
                        <label htmlFor="type" className="form-label"></label>
                        <select id="type" name="type" value={newItem.type} onChange={handleInputChange} className="form-input">
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </div>
                    <div>
                        <label htmlFor="quantity" className="form-label"></label>
                        <input id="quantity" name="quantity" type="number" value={newItem.quantity} onChange={handleInputChange} className="form-input" placeholder=": 10" />
                    </div>
                    <div>
                        <label htmlFor="receivedDate" className="form-label"></label>
                        <input id="receivedDate" name="receivedDate" type="date" value={newItem.receivedDate} onChange={handleInputChange} className="form-input" />
                    </div>
                    <button type="submit" className="action-button" style={{ height: '42px' }}>
                        <Plus size={18} style={{ marginRight: '0.5rem' }} />
                        
                    </button>
                </form>
            </div>

            {/*    */}
            <div className="card">
                <h3 style={{ fontSize: '1.125rem', fontWeight: '600', color: '#374151', marginBottom: '0.75rem' }}>
                    <Archive size={20} style={{ marginRight: '0.5rem', verticalAlign: 'bottom' }} />
                       ({inventory.length})
                </h3>
                <div className="table-container">
                    <table ref={tableRef} className="display nowrap" style={{ width: '100%' }}></table>
                </div>
            </div>
        </div>
    );
};

export default InventoryPage;
