import React, { useState, useEffect } from 'react';
import { Eye, EyeOff } from 'lucide-react';
import kaiaLogo from '../assets/image/kaia_logo.png';
import axios from 'axios'; // axios import 

const LoginPage = ({ onLogin }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [showPassword, setShowPassword] = useState(false);
    const [error, setError] = useState('');
    const [isEmployee, setIsEmployee] = useState(false);
    const [rememberId, setRememberId] = useState(false);

    useEffect(() => {
        const savedUsername = localStorage.getItem('rememberedUsername');
        if (savedUsername) {
            setUsername(savedUsername);
            setRememberId(true);
        }
    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');

        try {
            // form-data    FormData  .
            const formData = new FormData();
            formData.append('id', username);
            formData.append('passwd', password);

            // axios.post body formData .
            //   Axios  Content-Type  multipart/form-data .
            const response = await axios.post('/api/login', formData);

            //         .
            // response.data.list ,    login_chk "1"   .
            if (response.data && Array.isArray(response.data.list) && response.data.list.length > 0 && response.data.list[0].login_chk === "1") {
                //    ' '  .
                if (rememberId) {
                    localStorage.setItem('rememberedUsername', username);
                } else {
                    localStorage.removeItem('rememberedUsername');
                }
                onLogin(); // App.js onLogin     .
            } else {
                // API  ,     
                setError(response.data?.message || '    .');
            }
        } catch (err) {
            setError(err.response?.data?.message || '    .');
        }
    };

    return (
        <div className="login-page">
            <div className="login-card">
                <div style={{ marginBottom: '2.5rem' }}>
                    <img src={kaiaLogo} alt="KAIA Logo" style={{ width: '250px', margin: '0 auto 1.5rem', display: 'block' }} />
                    <hr style={{ border: 0, borderTop: '1px solid #e5e7eb' }} />
                </div>

                <div className="login-header-section">
                    <h2 className="login-title"></h2>
                </div>

                {error && (
                    <div className="login-error">
                        <strong style={{ fontWeight: 'bold' }}> !</strong>
                        <span style={{ display: 'block' }}> {error}</span>
                    </div>
                )}

                <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '1rem' }}>
                    <div>
                        <label htmlFor="username" style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.875rem', fontWeight: '500' }}></label>
                        <input 
                            id="username"
                            type="text" 
                            placeholder=" "
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            required 
                            className="form-input"
                        />
                    </div>

                    <div>
                        <label htmlFor="password" style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.875rem', fontWeight: '500' }}></label>
                        <div style={{ position: 'relative' }}>
                            <input
                                id="password"
                                type={showPassword ? 'text' : 'password'}
                                placeholder=" "
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required
                                className="form-input"
                                style={{ paddingRight: '2.5rem' }}
                            />
                            <button
                                type="button"
                                onClick={() => setShowPassword(!showPassword)}
                                className="password-toggle"
                                aria-label={showPassword ? " " : " "}
                            >
                                {showPassword ? <EyeOff size={20} /> : <Eye size={20} />}
                            </button>
                        </div>
                    </div>

                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', fontSize: '0.875rem', marginBottom: '0.5rem' }}>
                        {/* ID    UI    */}
                    </div>

                    <button 
                        type="submit" 
                        className="login-button"
                    ></button>
                </form>
            </div>
        </div>
    );
};

export default LoginPage;
