import React from 'react';
import { Search, Sliders, Check, Archive, X, LogOut, ChevronsLeft, ChevronsRight } from 'lucide-react';
import kaiaLogo from '../assets/image/kaia_logo.png';
import logoIcon from '../assets/image/logoicon.png'; //   

const Sidebar = ({ isSidebarOpen, setSidebarOpen, currentPage, setCurrentPage, setIsLoggedIn, selectedFarm, isCollapsed, setCollapsed }) => {
    const navItems = [
        { name: '', id: 'farmSearch', icon: Search, requiredFarm: false },
        { name: '', id: 'sireSelection', icon: Sliders, requiredFarm: true },
        { name: ' ', id: 'matingPlan', icon: Check, requiredFarm: true },
        { name: ' ', id: 'inventory', icon: Archive, requiredFarm: true },
    ];

    const handleNavClick = (pageId, requiredFarm) => {
        setCurrentPage(pageId, requiredFarm);
        setSidebarOpen(false); //     
    };

    return (
        <>
            {/*   */}
            {isSidebarOpen && (
                <div
                    className="sidebar-overlay"
                    onClick={() => setSidebarOpen(false)}
                ></div>
            )}
            
            <div
                className={`sidebar ${isSidebarOpen ? 'is-open' : ''} ${isCollapsed ? 'is-collapsed' : ''}`}
            >
                <div className="sidebar-header">
                    <img 
                        src={isCollapsed ? logoIcon : kaiaLogo} 
                        alt="KAIA Logo"
                        className={isCollapsed ? "sidebar-logo-icon" : "sidebar-logo-full"} />
                    <button onClick={() => setSidebarOpen(false)} className="sidebar-close-button">
                        <X size={24} />
                    </button>
                </div>

                <div className="sidebar-content">
                    <nav>
                        <ul style={{ listStyle: 'none', padding: 0, margin: 0, display: 'flex', flexDirection: 'column', gap: '0.5rem' }}>
                            {navItems.map(item => (
                                <li key={item.id}>
                                    <button
                                        onClick={() => handleNavClick(item.id, item.requiredFarm)}
                                        className={`nav-item ${currentPage === item.id ? 'active' : ''}`}
                                        title={isCollapsed ? item.name : ''}
                                    >
                                        <item.icon size={20} />
                                        <span className="nav-item-text">{item.name}</span>
                                    </button>
                                </li>
                            ))}
                        </ul>
                    </nav>

                    <div className="sidebar-footer">
                        <div className="selected-farm-info">
                            <div style={{ fontSize: '0.75rem', color: '#9ca3af', marginBottom: '0.5rem' }}> :</div>
                            {selectedFarm.id ? (
                                <div style={{ backgroundColor: '#eef2ff', color: '#4338ca', padding: '0.5rem', borderRadius: '0.375rem', fontSize: '0.875rem', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap', fontWeight: '500' }}>
                                    {selectedFarm.name} ({selectedFarm.id})
                                </div>
                            ) : (
                                <div style={{ backgroundColor: '#fef2f2', padding: '0.5rem', borderRadius: '0.375rem', fontSize: '0.875rem', color: '#b91c1c' }}>
                                     .
                                </div>
                            )}
                        </div>
                        <button onClick={() => setIsLoggedIn(false)} className="logout-button sidebar">
                            <LogOut size={20} />
                            <span className="nav-item-text"></span>
                        </button>
                        <button onClick={() => setCollapsed(!isCollapsed)} className="sidebar-toggle-button">
                            {isCollapsed ? <ChevronsRight size={20} /> : <ChevronsLeft size={20} />}
                            <span className="nav-item-text"></span>
                        </button>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Sidebar;
