import React from 'react';

const CustomDialog = ({ title, message, isOpen, onClose, onConfirm }) => {
    if (!isOpen) return null;

    return (
        <div className="dialog-overlay">
            <div className="dialog-box">
                <h3 className="dialog-title">{title}</h3>
                <p style={{ fontSize: '0.875rem', color: '#374151', marginBottom: '1.5rem' }}>{message}</p>
                <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '0.75rem' }}>
                    {onConfirm && (
                        <button
                            onClick={onClose}
                            className="dialog-button dialog-cancel"
                        >
                            
                        </button>
                    )}
                    <button
                        onClick={onConfirm || onClose}
                        className="dialog-button dialog-confirm"
                    >
                        
                    </button>
                </div>
            </div>
        </div>
    );
};

export default CustomDialog;
