import React, { useState, useEffect, useRef } from 'react';
import { Search, Check } from 'lucide-react';
import { mockFarms } from '../data/mockData';
import $ from 'jquery';
import 'datatables.net-dt';

// DataTables      
import 'datatables.net-dt/css/dataTables.dataTables.css';
import 'datatables.net-responsive-dt/css/responsive.dataTables.css';

// DataTables    
import 'datatables.net-responsive';

const FarmSearchPage = ({ selectedFarm, setSelectedFarm, setCurrentPage }) => {
    const tableRef = useRef(null);
    const dtApi = useRef(null);

    const [searchQuery, setSearchQuery] = useState('');
    const [searchResults, setSearchResults] = useState(mockFarms);

    const handleSearch = () => {
        const query = searchQuery.trim().toLowerCase();
        if (!query) {
            setSearchResults(mockFarms);
            return;
        }
        
        const filtered = mockFarms.filter(farm => 
            farm.name.toLowerCase().includes(query) || 
            farm.phone.includes(query)
        );
        setSearchResults(filtered);
    };

    const handleSelectFarm = (farm) => {
        setSelectedFarm(farm);
        alert(`'${farm.name}'  .      .`);
        //      ,     .
        // setCurrentPage('sireSelection', true); 
    };

    const columns = [
        { title: '', data: 'name' },
        { title: ' ID', data: 'id' },
        { title: '', data: 'phone' },
        { title: '', data: 'owner' },
        { title: '', data: 'id' }
    ];

    useEffect(() => {
        const options = {
            columns: columns,
            responsive: {
                details: { type: 'inline' }
            },
            paging: true,
            lengthChange: false,
            pageLength: 10,
            searching: false,
            ordering: true,
            language: {
                emptyTable: "  .",
                paginate: { first: "", last: "", next: "", previous: "" },
                info: "_TOTAL_  _START_ _END_ ",
                infoEmpty: "0",
                infoFiltered: "(_MAX_ )"
            },
            columnDefs: [
                { responsivePriority: 2, targets: 0 }, // 
                { responsivePriority: 3, targets: 1 }, //  ID
                {
                    targets: 4, //  
                    responsivePriority: 1,
                    orderable: false,
                    className: 'dt-body-right',
                    render: (data, type, row) => {
                        //   ,   drawCallback .
                        return `<button class="datatable-action-button secondary-button" data-farm-id="${row.id}"></button>`;
                    }
                }
            ],
            drawCallback: function(settings) {
                const api = this.api();
                api.responsive.recalc();

                //    
                api.rows({ page: 'current' }).nodes().to$().each(function() {
                    const row = $(this);
                    const rowData = api.row(this).data();
                    
                    // rowData  
                    if (rowData && selectedFarm && rowData.id === selectedFarm.id) {
                        row.css('background-color', '#eff6ff');
                        const actionCell = row.find('td:last-child');
                        if (actionCell.find('.datatable-action-button').length > 0) {
                           actionCell.html(`<span class="datatable-completed-span" style="justify-content: flex-end;"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><polyline points="20 6 9 17 4 12"></polyline></svg> </span>`);
                        }
                    } else {
                        row.css('background-color', '');
                    }
                });
            }
        };

        dtApi.current = $(tableRef.current).DataTable(options);

        $(tableRef.current).on('click', '.datatable-action-button', function() {
            const farmId = $(this).data('farm-id');
            const farmToSelect = searchResults.find(f => f.id === farmId);
            if (farmToSelect) {
                handleSelectFarm(farmToSelect);
            }
        });

        return () => {
            if (dtApi.current) {
                $(tableRef.current).off('click');
                dtApi.current.destroy();
                dtApi.current = null;
            }
        };
    }, []); //     

    //       
    useEffect(() => {
        if (dtApi.current) {
            dtApi.current.clear().rows.add(searchResults).draw();
        }
    }, [searchResults]);

    //        (UI )
    useEffect(() => {
        if (dtApi.current) {
            dtApi.current.draw(false);
        }
    }, [selectedFarm]);

    return (
        <div className="page-content" style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
            <h2 className="page-title">   </h2>
            
            <div className="card">
                <div className="search-bar-container">
                    <input
                        type="search"
                        placeholder="   "
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
                        className="form-input"
                    />
                    <button
                        onClick={handleSearch}
                        className="action-button"
                        style={{ padding: '0.75rem 1rem' }}
                    >
                        <Search size={20} style={{ marginRight: '0.5rem' }} />
                        
                    </button>
                </div>
            </div>

            {selectedFarm && selectedFarm.id && (
                <div className="card" style={{ backgroundColor: '#ecfdf5', border: '1px solid #a7f3d0' }}>
                    <p style={{ fontWeight: '600', color: '#047857' }}>   : <span style={{ color: '#059669' }}>{selectedFarm.name} ({selectedFarm.id})</span></p>
                </div>
            )}

            <div className="card">
                <h3 style={{ fontSize: '1.125rem', fontWeight: '600', color: '#374151', marginBottom: '0.75rem' }}>  ({searchResults.length})</h3>
                <div className="table-container">
                    <table ref={tableRef} className="display nowrap" style={{ width: '100%' }}>
                        {/*   DataTables . */}
                    </table>
                </div>
            </div>
        </div>
    );
};

export default FarmSearchPage;
