import React, { useState, useEffect, useRef, useMemo } from 'react';
import { ChevronLeft, ChevronRight, Trash2, ListChecks, CheckSquare, X } from 'lucide-react';
import MatingPlanResult from '../components/mating/MatingPlanResult';
import { mockCows } from '../data/mockData'; //     
import $ from 'jquery';
import 'datatables.net-dt';

// DataTables      
import 'datatables.net-dt/css/dataTables.dataTables.css';
import 'datatables.net-responsive-dt/css/responsive.dataTables.css';

// DataTables    
import 'datatables.net-responsive';

const MatingPlanPage = ({ planState, setPlanState, selectedFarm, setDialog }) => {
    const [currentStep, setCurrentStep] = useState(1);
    const totalSteps = 3;

    const handleNext = () => {
        if (currentStep === 1 && planState.selectedSires.length === 0) {
            setDialog({ isOpen: true, title: "", message: " 1   .", onClose: () => setDialog({ isOpen: false }) });
            return;
        }
        if (currentStep === 2 && planState.selectedCows.length === 0) {
            setDialog({ isOpen: true, title: "", message: "  1   .", onClose: () => setDialog({ isOpen: false }) });
            return;
        }
        setCurrentStep(prev => Math.min(prev + 1, totalSteps));
    };

    const handlePrev = () => {
        setCurrentStep(prev => Math.max(prev - 1, 1));
    };

    const renderStepContent = () => {
        switch (currentStep) {
            case 1:
                return <SireInputAndList 
                    planState={planState} 
                    setPlanState={setPlanState} 
                    selectedFarm={selectedFarm} 
                    setDialog={setDialog}
                />;
            case 2:
                return <CowSelectionList 
                    planState={planState} 
                    setPlanState={setPlanState} 
                    selectedFarm={selectedFarm} 
                    setDialog={setDialog}
                />;
            case 3:
                return <MatingPlanResult 
                    planState={planState} 
                    setPlanState={setPlanState} 
                    setCurrentStep={setCurrentStep}
                />;
            default:
                return <div>   .</div>;
        }
    };

    return (
        <div className="page-content" style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
            <h2 className="page-title"> </h2>
            
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', fontSize: '0.875rem', fontWeight: '500' }}>
                {[
                    { step: 1, name: ' /' },
                    { step: 2, name: '  ' },
                    { step: 3, name: ' ' },
                ].map(item => (
                    <div 
                        key={item.step}
                        onClick={() => setCurrentStep(item.step)}
                        style={{ 
                            flex: 1, 
                            textAlign: 'center', 
                            padding: '0.5rem 0', 
                            borderBottom: '4px solid', 
                            transition: 'border-color 0.3s', 
                            cursor: 'pointer',
                            borderColor: currentStep === item.step ? '#4f46e5' : '#e5e7eb',
                            color: currentStep === item.step ? '#4f46e5' : '#6b7280',
                            fontWeight: currentStep === item.step ? 'bold' : 'normal',
                        }}
                    >
                        Step {item.step}. {item.name}
                    </div>
                ))}
            </div>

            <div style={{ backgroundColor: 'white', padding: '1.5rem', borderRadius: '0.5rem', boxShadow: '0 10px 15px -3px rgba(0, 0, 0, 0.1)', minHeight: '400px' }}>
                {renderStepContent()}
            </div>

            <div style={{ display: 'flex', justifyContent: 'space-between', paddingTop: '1rem' }}>
                <button
                    onClick={handlePrev}
                    disabled={currentStep === 1}
                    className="secondary-button"
                    style={{ padding: '0.75rem 1.5rem' }}
                >
                    <ChevronLeft size={20} style={{ marginRight: '0.5rem' }} />  
                </button>
                <button
                    onClick={handleNext}
                    disabled={currentStep === totalSteps}
                    className="action-button"
                    style={{ padding: '0.75rem 1.5rem' }}
                >
                    {currentStep === totalSteps - 1 ? ' ' : ' '} <ChevronRight size={20} style={{ marginLeft: '0.5rem' }} />
                </button>
            </div>
        </div>
    );
};

const SireInputAndList = ({ planState, setPlanState, selectedFarm, setDialog }) => {
    const tableRef = useRef(null);
    const dtApi = useRef(null);

    const handleDeleteSire = (sireId) => {
        setDialog({
            isOpen: true,
            title: " ",
            message: `${sireId}   ?`,
            onConfirm: () => {
                setPlanState(prev => ({
                    ...prev,
                    selectedSires: prev.selectedSires.filter(s => s.id !== sireId)
                }));
                setDialog({ isOpen: false });
            },
            onClose: () => setDialog({ isOpen: false })
        });
    };

    const columns = [
        { title: '', data: 'id' },
        { title: '', data: 'flawTraits' },
        { title: '', data: 'name' },
        { title: '', data: 'betacasein' },
        { title: '', data: 'kappacasein' },
        { title: '', data: 'id' }
    ];

    useEffect(() => {
        const options = {
            columns: columns,
            responsive: { details: { type: 'inline' } },
            paging: false,
            searching: false,
            ordering: true,
            info: false,
            language: { emptyTable: "  ." },
            columnDefs: [
                { responsivePriority: 2, targets: 0 },
                {
                    targets: 1,
                    render: (data) => Array.isArray(data) && data.length > 0 ? data.join(', ') : ''
                },
                {
                    targets: 5,
                    responsivePriority: 1,
                    orderable: false,
                    className: 'dt-body-center',
                    render: (data) => `<button class="datatable-action-button danger-button" data-sire-id="${data}"><svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><polyline points="3 6 5 6 21 6"></polyline><path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path><line x1="10" y1="11" x2="10" y2="17"></line><line x1="14" y1="11" x2="14" y2="17"></line></svg></button>`
                }
            ],
            drawCallback: function() {
                this.api().responsive.recalc();
            }
        };

        dtApi.current = $(tableRef.current).DataTable(options);

        $(tableRef.current).on('click', '.datatable-action-button', function() {
            const sireId = $(this).data('sire-id');
            handleDeleteSire(sireId);
        });

        return () => {
            if (dtApi.current) {
                $(tableRef.current).off('click');
                dtApi.current.destroy();
                dtApi.current = null;
            }
        };
    }, []);

    useEffect(() => {
        if (dtApi.current) {
            dtApi.current.clear().rows.add(planState.selectedSires).draw();
        }
    }, [planState.selectedSires]);

    return (
        <div className="card">
            <h4 style={{ display: 'flex', alignItems: 'center', fontWeight: '600', marginBottom: '1rem' }}>
                <ListChecks size={18} style={{ marginRight: '0.5rem' }} />
                    ({planState.selectedSires.length})
            </h4>
            <div className="table-container">
                <table ref={tableRef} className="display nowrap" style={{ width: '100%' }}></table>
            </div>
        </div>
    );
};

const CowSelectionList = ({ planState, setPlanState, selectedFarm, setDialog }) => {
    const tableRef = useRef(null);
    const dtApi = useRef(null);
    //         .
    const availableCows = useMemo(() => {
        return mockCows.filter(cow => cow.farmId === selectedFarm.id);
    }, [selectedFarm.id]);
    const selectedCowIdsRef = useRef(new Set());

    const currentCowIds = useMemo(() => new Set(planState.selectedCows.map(c => c.id)), [planState.selectedCows]);

    useEffect(() => {
        selectedCowIdsRef.current = currentCowIds;
        if (dtApi.current) {
            dtApi.current.draw(false);
        }
    }, [currentCowIds]);

    const handleToggleCowSelection = (cow) => {
        const isSelected = selectedCowIdsRef.current.has(cow.id);
        setPlanState(prev => {
            const newSelectedCows = isSelected
                ? prev.selectedCows.filter(c => c.id !== cow.id)
                : [...prev.selectedCows, cow];
            return { ...prev, selectedCows: newSelectedCows };
        });
    };

    const columns = [
        { title: '', data: null, defaultContent: '' },
        { title: ' ', data: 'id' },
        { title: '', data: 'name' },
        { title: '', data: 'flawTraits' },
        { title: '', data: 'birthDate' },
    ];

    useEffect(() => {
        const options = {
            columns: columns,
            responsive: { details: { type: 'inline' } },
            paging: true,
            lengthChange: false,
            pageLength: 5,
            searching: false,
            ordering: true,
            language: {
                emptyTable: "   .",
                paginate: { first: "", last: "", next: "", previous: "" },
                info: "_TOTAL_  _START_ _END_ ",
                infoEmpty: "0",
            },
            columnDefs: [
                {
                    targets: 0,
                    responsivePriority: 1,
                    orderable: false,
                    className: 'dt-body-center',
                    render: (data, type, row) => `<input type="checkbox" class="datatable-checkbox" data-cow-id="${row.id}" />`
                },
                { responsivePriority: 2, targets: 1 },
                {
                    targets: 3,
                    render: (data) => Array.isArray(data) && data.length > 0 ? data.join(', ') : ''
                }
            ],
            drawCallback: function() {
                const api = this.api();
                api.responsive.recalc();

                api.rows({ page: 'current' }).nodes().to$().each(function() {
                    const row = $(this);
                    const rowData = api.row(this).data();
                    const checkbox = row.find('.datatable-checkbox');
                    
                    if (rowData && selectedCowIdsRef.current.has(rowData.id)) {
                        row.css('background-color', '#eff6ff');
                        checkbox.prop('checked', true);
                    } else {
                        row.css('background-color', '');
                        checkbox.prop('checked', false);
                    }
                });
            }
        };

        dtApi.current = $(tableRef.current).DataTable(options);

        $(tableRef.current).on('change', '.datatable-checkbox', function() {
            const cowId = $(this).data('cow-id');
            const cowToToggle = availableCows.find(c => c.id === cowId);
            if (cowToToggle) {
                handleToggleCowSelection(cowToToggle);
            }
        });

        return () => {
            if (dtApi.current) {
                $(tableRef.current).off('change');
                dtApi.current.destroy();
                dtApi.current = null;
            }
        };
    }, []);

    useEffect(() => {
        if (dtApi.current) {
            dtApi.current.clear().rows.add(availableCows).draw();
        }
    }, [availableCows]);

    return (
        <div className="card">
            <h4 style={{ display: 'flex', alignItems: 'center', fontWeight: '600', marginBottom: '1rem' }}>
                <ListChecks size={18} style={{ marginRight: '0.5rem' }} />
                    ({selectedFarm.name})
            </h4>
            <div className="table-container">
                <table ref={tableRef} className="display nowrap" style={{ width: '100%' }}></table>
            </div>
        </div>
    );
};

export default MatingPlanPage;
